* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar-body {
  width: 100%;
  min-height: fit-content;
  font-family: "Roboto", sans-serif;
  z-index: 9;
}

.navbar-menu {
  list-style: none;
}
.navbar-menu-links {
  text-decoration: none;
}
.navbar-header {
  width: 100%;
  min-height: 80px;
  padding: 15px 50px;
}
.navbar-container {
  width: 100%;
  margin: 0 auto;
}
.navbar-nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.navbar-nav .navbar-logo {
  color: #12232E;
  font-weight: 700;
  position: relative;
  z-index: -1;
  text-decoration: none;
  height: 60px;
  width: 120px;
}
.navbar-nav .navbar-menu .navbar-menu-li {
  display: inline-block;
}
.navbar-nav .navbar-menu .navbar-menu-li .navbar-menu-links {
  display: block;
  color: #12232E;
  font-size: 18px;
  text-transform: uppercase;
  padding: 10px 20px;
  margin: 0 5px;
  position: relative;
}

.navbar-nav .navbar-barIcon {
  font-size: 30px;
  color: #12232E;
  cursor: pointer;
  position: relative;
  z-index: 9;
  transition: 0.3s;
  display: none;
}
#navbar-menuBtn {
  display: none;
}

/* Main */
.navbar-banner {
  margin: 0px 60px;
}
.navbar-banner img {
  width: 100%;
  height: 450px;
}

/* Search Bar */
.navbar-search {
  margin: auto;
  width: 30%;
  margin-top: -30px;
}
.navbar-search-box {
  width: 450px;
  position: relative;
  display: flex;
}
.navbar-search-input {
  width: 100%;
  padding: 10px;
  border: 2px solid #12232E;
  border-right: none;
  outline: none;
  font-size: 20px;
  color: #12232E;
  background: #EEFBFB;
}
.navbar-search-button {
  text-align: center;
  height: 51px;
  width: 40px;
  outline: none;
  cursor: pointer;
  border: 2px solid #12232E;
  border-left: none;
  background: none;
  font-size: 17px;
  background-color: #12232E;
  color: #EEFBFB;
}

/* mobile breakpoint start */
@media screen and (max-width: 1250px) {
  .navbar-banner {
    margin: 0px 20px;
  }
  .navbar-header {
    min-height: fit-content;
    padding: 8px 25px;
  }
  .navbar-nav .navbar-logo {
    height: 50px;
    width: 70px;
  }
  .navbar-banner img {
    height: 400px;
  }
  .navbar-search {
    width: auto;
    margin: 0 50px;
    margin-top: -30px;
  }
  .navbar-search-box {
    width: auto;
  }
}

@media only screen and (max-width: 991px) {
  .navbar-nav .navbar-menu .navbar-menu-li .navbar-menu-links {
    font-size: 16px;
    padding: 10px 15px;
  }
  .navbar-header {
    min-height: fit-content;
    padding: 8px 25px;
  }
  .navbar-nav .navbar-logo {
    height: 50px;
    width: 70px;
  }
}
@media only screen and (max-width: 768px) {
  .navbar-nav .navbar-barIcon {
    display: block;
    cursor: pointer;
  }
  .navbar-header {
    min-height: fit-content;
    padding: 8px 25px;
  }
  .navbar-nav .navbar-logo {
    height: 50px;
    width: 70px;
  }
  .navbar-nav .navbar-menu {
    position: fixed;
    top: 66px;
    left: 0;
    width: 100%;
    background: #111;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transform: translateX(-100%);
    transition: 0.3s;
    z-index: 1;
  }
  .navbar-nav .navbar-menu .navbar-menu-li {
    margin: 10px 0;
  }
  .navbar-nav .navbar-menu .navbar-menu-li .navbar-menu-links {
    display: block;
    font-size: 20px;
    padding: 15px 25px;
    color: #fff;
  }
  .navbar-nav .navbar-menu .navbar-menu-li:first-child {
    margin-top: 0;
  }
  #navbar-menuBtn:checked ~ .navbar-menu {
    transform: translateX(0);
  }

  /* Main */
  .navbar-banner {
    margin: 0px !important;
  }
  .navbar-banner img {
    height: auto;
  }
  .navbar-search {
    width: auto;
    margin: 0 50px;
    margin-top: -30px;
  }
  .navbar-search-box {
    width: auto;
  }
}
