@import url('https://fonts.googleapis.com/css2?family=Cairo:wght@400&display=swap');
:root{
	--of-white:  #EEFBFB;
	--primary: #007CC7;
}
*{
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

.blog-container{
	width: 88%;
	margin: 2rem auto;
}
.blog-card{
	background-color:  #EEFBFB;
	box-shadow: 0 0 2.5rem #12232E;
}
.blog-card-img{
	overflow: hidden;
	height: auto;
}
.blog-card-img .blog-img{
	width: 100%;
	height: 100%;
	object-fit: cover;
	display: auto;
}
.blog-card-content{
	padding: 2rem;
}
.blog-details{
	display: flex;
	font-size: 14px;
}
.blog-icon-text{
	display: flex;
	align-items: center;
  margin-right: 10px;
  font-size: 15px;
}
.blog-icon-date{
  margin-left: 30px;
 font-size: 15px;
}
.blog-icon-text .blog-icon{
	margin: 4px 4px 4px 4px;
	color: var(--primary);
  font-size: 15px;
}
.blog-title{
	font-weight: bolder;
	text-align: justify;
}
.blog-content{
	padding-bottom: 8px;
  margin: 35px;
  text-align: justify;
}
.blog{
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-top: 10px;
}
.blog-icon-bt .blog-icon{
	margin-right: 2px;
	color: #12232E;
}
.blog-cta{
	margin-top: 20px;
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.blog-btn{
	outline: none;
	padding: 10px 15px;
	font-weight: bolder;
	cursor: pointer;
	background-color: #007CC7;
	color: #EEFBFB;
	border-radius: 5px;
	border:1px solid #007CC7;
}
.blog-share-button{
	width: 2rem;
	height: 2rem;
	border: 0.1rem solid var(--primary);
	border-radius: 50%;
	background-color: transparent;
	cursor: pointer;
	color: #007CC7;
	outline: none;
}
#more{
	display: none;
}


@media screen and (min-width: 800px){
	.blog-container{
		width: 100%;
		margin: 2rem auto;
	}

	.blog-card{
		display: flex;
		overflow: hidden;
		height: auto;
        font-size: 20px;
	}
	.blog-card-img{
		height: auto;
		flex: 1;
    font-size: 30px;
	}
	.blog-card-content{
		flex: 2;
		display: flex;
		flex-direction: column;
		justify-content: center;
    font-size: 20px;
	}
  
	
}