@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;500;600;700&display=swap');
*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}
.aboutus-body{
    font-family: 'Playfair Display', serif;
    display: grid;
   background-color: #4da8da;
    align-content: center;
    min-height: fit-content;
	width: 88%;
   margin: auto;
   margin-bottom: 30px;
}
.aboutus-section{
    display: grid;
    grid-template-columns: 1fr 1fr;
   width: 88%;
}
.aboutus-image{ 
    display: flex;  
	margin: auto;
	width: 60%;
}
.aboutus-image img{  
   margin-right: 10px;
   height: 300px;
   width: 150px;
}
.aboutus-image .aboutus-image2{
   margin-top: 20px;
}
.aboutus-image .aboutus-image4{
    margin-top: 20px;
 }
.aboutus-content{
    background-color:#4da8da;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    color: rgb(20, 20, 20);
    margin: 70px 0px 60px 100px;
}
.aboutus-content h2{
    text-transform: uppercase;
    font-size: 36px;
    letter-spacing: 6px;
    opacity: 0.9;
    color: #000000;
    margin-right: 30%;
}

.aboutus-content p{
    padding-bottom: 15px;
    font-weight: 300;
    opacity: 0.7;
    text-align: justify;
    line-height: 1.7;
    color:#000000
}
.links{
    margin: 15px 0;
}
.links li{
    border: 2px solid #f7f7f7;
    list-style: none;
    border-radius: 5px;
    padding: 10px 15px;
    width: 160px;
    text-align: center;
}
.links li a{
    text-transform: uppercase;
    color: #fff;
    text-decoration: none;
}
.links li:hover{
    border-color: #585ae7;
}

.vertical-line{
    height: 30px;
    width: 3px;
    background: #5066c8;
    margin: 0 auto;
}
.aboutus-icons{
    display: flex;
    padding: 15px 0;
    color: #000000;
    margin-right: 30%;
}
.aboutus-icons li{
    display: block;
    padding: 5px;
    margin: 5px;
}
.aboutus-icons li i{
    font-size: 26px;
    opacity: 0.8;
}
.aboutus-icons li i:hover{
    color: #5250c8;
    cursor: pointer;
}


/*******/

@media(max-width: 900px){
	.aboutus-body{
		align-content: center;
		min-height: fit-content;
		width: 100%;
		margin-bottom: 30px;
	}
    .aboutus-section{
        grid-template-columns: 1fr;
        width: 100%;
        border-radius: none;
    }
    .aboutus-image{ 
		display: flex;  
		margin: 7px;
	  width: 100%;
	}
	.aboutus-image img{  
	   margin-right: 7px;
	   height: 150px;
	   width: 70px;
	   
	}
	.aboutus-image .aboutus-image2{
	   margin-top: 20px;
	}
	.aboutus-image .aboutus-image4{
		margin-top: 20px;
	 }
    .aboutus-content{
        border-radius: none;
        margin: auto;
		margin-top: 20px;
    }
    .aboutus-content h2{
        font-size: 20px;
        margin: auto;
		margin-bottom: 10px;
    }
    .aboutus-content span{
        margin-left: 30%;
        margin: 20px 0;
    }
    .aboutus-content p{
        font-size: 14px;
		margin-left: 15px;
		margin-right: 15px;
    }
    .links li a{
        font-size: 14px;
        margin-left: 30%;
    }
    .links{
        margin: 5px 0;
        
    }
    .links li{
        padding: 6px 10px;
        
    }
    .aboutus-icons{
		margin: auto;
    }
    .aboutus-icons li i{
        font-size: 15px;
       
    }
}